const React = require('react');

const CurrentFileInfoView = React.createClass({
	render(){
		return <div className='current-file-info'>
			<div>
				{
					this.props.file
					? `${this.props.file.name} (${this.props.file.sha1})`
					: 'No audio file loaded. Please drop an audio file into this window.'
				}
			</div>
		</div>;
	}
});

module.exports = CurrentFileInfoView;



