const Immutable = require('immutable');
const Chunk = require('chunk');
const DataCore = require('data-core');

let lastId = 0;

resetChunks();

function resetChunks(){
	setChunks(Immutable.List.of(createChunk({ time: 0 })));
}

function createChunk(args){ // factory only, doesn't add to the list of all chunks
	args = args || {};
	if (typeof args.time !== 'number'){
		args.time = getCurrentTime();
	}
	args.time = +args.time.toFixed(2);
	args.id = ++lastId;
	return new Chunk(args);
}

function setChunks(newChunks){
	DataCore.set('/chunks/all', newChunks); // assertion: this is the ONLY place that assigns to /chunks/all!
}

function chunkForTime(chunks, time){
	// TODO: optimize
	return chunks.findLast(c => c.time <= time);
}

function chunkBeforeForTime(chunks, time){
	const index = chunks.findLastIndex(c => c.time <= time);
	if (index == 0){
		return null;
	}
	else {
		return chunks.get(index - 1);
	}
}

function chunkAfterForTime(chunks, time){
	const index = chunks.findLastIndex(c => c.time <= time);
	if (index == chunks.size - 1){
		return null;
	}
	else {
		return chunks.get(index + 1);
	}
}

function updateChunk(chunks, chunk){
	const index = chunks.findLastIndex(c => c.time <= chunk.time);
	const existingChunk = chunks.get(index);

	return existingChunk.time === chunk.time
	? chunks.set(index, chunk)
	: chunks.splice(index + 1, 0, chunk);
}

function withoutChunk(chunks, time){
	return chunks.filter(c => c.time !== time);
}

function allChunks(){
	return DataCore.get('/chunks/all');
}

DataCore.provide('/chunks/current',
	DataCore.observeMany({
		time: '/time',
		chunks: '/chunks/all'
	})
	.filter(o => o.chunks && typeof o.time === 'number')
	.map(inputs => chunkForTime(inputs.chunks, inputs.time))
);

module.exports = {
	set: setChunks,
	create: createChunk,
	reset: resetChunks,

	save(chunk){
		setChunks(updateChunk(allChunks(), chunk))
	},

	get(time){
		return chunkForTime(allChunks(), time);
	},

	getAll(){
		return allChunks();
	},

	delete(time){
		setChunks(withoutChunk(allChunks(), time));
	},

	getChunkBefore(time){
		return chunkBeforeForTime(allChunks(), time);
	},

	getChunkAfter(time){
		return chunkAfterForTime(allChunks(), time);
	},

	move(chunk, newTime){
		if (chunk.time > 0 && chunk.time != newTime){ // first chunk can't move
			newTime = +newTime.toFixed(2);
			const newChunk = chunk.set('time', newTime);
			setChunks(withoutChunk(updateChunk(allChunks(), newChunk), chunk.time));
		}
	}
};



