const Immutable = require('immutable');

const PathObserverType = Immutable.Record({
  crackedPath: null,
  subject: null
});

const PathProviderType = Immutable.Record({
  crackedPath: null,
  disposable: null
});

const DataCore = new function DataCore(){
  const self = this;
  var dataRoot = Immutable.Map();
  var pathObservers = Immutable.List();
  var pathProviders = Immutable.List();

  function crackPath(path){ // returns Immutable.List
    if (path[0] !== '/'){
      throw "path must start with /";
    }

    return Immutable.Seq(path.split(/\/+/))
    .filter(s => s !== '')
    .flatMap(s => {
      const m = s.match(/^(.+)\[(\d+)\]$/);
      if (m){
        return [m[1], parseInt(m[2], 10)];
      }
      else {
        return [s];
      }
    })
    .toList();
  }

  self.crackPath = crackPath;

  function pathString(path){ // path is Immutable.List()
    return '/' + path.toArray().join('/')
  }

  self.pathString = pathString;

  function signalChange(crackedPath){
    pathObservers
    .filter(function(pathObserver){
      const matchedPartCount =
        pathObserver.crackedPath
        .zip(crackedPath)
        .takeWhile(function(item){ return item[0] === item[1] })
        .size;

      return matchedPartCount === pathObserver.crackedPath.size || matchedPartCount === crackedPath.size;
    })
    .forEach(function(pathObserver){
      pathObserver.subject.onNext(dataRoot.getIn(pathObserver.crackedPath));
    });
  }

  self.observe = function DataCoreObserve(path){
    return Rx.Observable.create(function(observer){
      const crackedPath = crackPath(path);

      const pathObserver = new PathObserverType({
        crackedPath: crackedPath,
        subject: new Rx.Subject
      });
      pathObservers = pathObservers.push(pathObserver);

      const disposables = new Rx.CompositeDisposable;
      disposables.add(
        Rx.Disposable.create(function(){
          pathObservers = pathObservers.filter(function(o){ return o !== pathObserver });
        })
      );

      disposables.add(
        pathObserver.subject
        .distinctUntilChangedImmutable()
        .subscribe(observer)
      );

      pathObserver.subject.onNext(dataRoot.getIn(crackedPath));

      return disposables;
    });
  }


  // TODO: optimize
  self.observeMany = function(pathsByKey){
    const crackedPathsByKey = Immutable.Map(pathsByKey).map(function(path){ return crackPath(path) });

    return self.observe('/')
    .map(function(root){
      return crackedPathsByKey
      .map(function(crackedPath){ return root.getIn(crackedPath) });
    })
    .distinctUntilChangedImmutable()
    .map(o => o.toObject());
  };

  self.logWrites = false;

  function setPathValue(crackedPath, value){
    if (self.logWrites){
      var printable = value;
      if (Immutable.Iterable.isIterable(value)){
        printable = value.toJS();
      }
      console.log(pathString(crackedPath), printable);
    }

    if (crackedPath.size === 0){
      throw "Cannot set the root path"
    }

    const newValue = Immutable.fromJS(value);

    var newRoot;
    if (newValue === undefined){
      newRoot = dataRoot.deleteIn(crackedPath);
    }
    else {
      newRoot = dataRoot.setIn(crackedPath, newValue);
    }
    if (!Immutable.is(dataRoot, newRoot)){
      dataRoot = newRoot.update('data-core-revision', 0, n => n + 1);
      signalChange(crackedPath);
      signalChange(['data-core-revision']);
    }
  }


  self.set = function dataCoreSet(path, value){
    const crackedPath = crackPath(path);
    const wouldStompOver =
      pathProviders
      .map(prov => {
        const matchedPathPartCount =
          prov.crackedPath
          .zip(crackedPath)
          .takeWhile(([a, b]) => a === b)
          .size;

        if (matchedPathPartCount == crackedPath.size || matchedPathPartCount == prov.crackedPath.size){
          return prov.crackedPath;
        }
      })
      .filter(o => o !== undefined)
      .map(crackedPath => pathString(crackedPath));

    if (wouldStompOver.size > 0){
      throw "DataCore.set error: would stomp over " + wouldStompOver.toArray().join(', ');
    }

    setPathValue(crackedPath, value);
  };

  self.get = function dataCoreGet(path, notSetValue){
    const crackedPath = crackPath(path);
    return dataRoot.getIn(crackedPath, notSetValue);
  };

  self.provide = function dataCoreProvide(path, observable){
    const crackedPath = crackPath(path);

    const pathProvider = new PathProviderType({
      crackedPath: crackedPath,
      disposable: new Rx.CompositeDisposable
    });
    pathProviders = pathProviders.push(pathProvider);

    pathProvider.disposable.add(
      observable
      .startWith(null)
      .distinctUntilChangedImmutable()
      .subscribe(
        value => setPathValue(crackedPath, value),
        err => console.error('DataCore provider for ' + path + ' onError:', err),
        () => console.warn('DataCore provider for ' + path + ' completed')
      )
    );

    pathProvider.disposable.add(
      Rx.Disposable.create(function(){
        pathProviders = pathProviders.filter(o => o !== pathProvider);
        self.set(path, undefined);
      })
    );

    return pathProvider.disposable;
  };
};

module.exports = DataCore;



