const Immutable = require('immutable');
const DataCore = require('data-core');

DataCore.observe('/settings')
.filter(o => o)
.do(settings => localStorage.settings = JSON.stringify(settings))
.run('auto save settings');

if (localStorage.settings){
	try{
		DataCore.set('/settings', Immutable.fromJS(JSON.parse(localStorage.settings || '{}')));
	}
	catch (e){
		console.error(`Error parsing settings ${localStorage.settings}: ${e}`);
	}
}



