const Immutable = require('immutable');
const React = require('react');
const ReactUtils = require('react-utils');
const DataCore = require('data-core');
const Chunks = require('chunks');


const CommandsView = React.createClass({
	mixins: [ReactUtils.RxMixin, ReactUtils.DataCoreMixin],

	getInitialState(){
		return {
			activeCommands: Immutable.Set(),
		};
	},

	componentWillMount(){
		this.bindToDataCore({
			visible: '/settings/show-keyboard'
		});

		this.commands = Immutable.OrderedMap();

		const commandWasRun = new Rx.Subject;

		const keyboardCommand = (keys, desc, handler) => {
			this.commands = this.commands.set(keys, desc);

			Mousetrap.bind(keys, e => {
				this.setState({ activeCommands: this.state.activeCommands.add(keys) });
				commandWasRun.onNext(keys);

				e.preventDefault();
				e.stopPropagation();
				handler(e);
			});
		};

		Immutable.Range(1, 10)
		.forEach(i =>
			Mousetrap.bind(`ctrl+${i}`, e => {
				const chunk = getCurrentChunk();
				Chunks.save(chunk.set('speaker', String(i)));

				e.preventDefault();
				e.stopPropagation();
			})
		);

		keyboardCommand('ctrl+k', 'Show/hide this information about keyboard shortcuts', () =>
			DataCore.set('/settings/show-keyboard', !this.state.visible)
		);

		keyboardCommand('ctrl+p', 'Play current chunk from beginning', () => {
			setCurrentTime(getCurrentChunk().time);
			play();
		});

		keyboardCommand('ctrl+s', 'Pause/unpause', () => {
			if (isPlaying()){
				pause();
			}
			else {
				play();
			}
		});

		keyboardCommand('ctrl+enter', 'Create new chunk at the current time', () => {
			const chunk = getCurrentChunk();
			const currentTime = getCurrentTime();
			if (chunk.time !== currentTime){
				const newChunk = Chunks.create({ time: currentTime, speaker: chunk.speaker });
				Chunks.save(newChunk);
			}
		});

		keyboardCommand('ctrl+[', 'Move current chunk back  second', () => {
			const currentChunk = getCurrentChunk();
			const prevChunk = Chunks.getChunkBefore(currentChunk.time);
			if (prevChunk){
				const newTime = currentChunk.time - 0.5;
				if (newTime > prevChunk.time){
					setCurrentTime(newTime);
					Chunks.move(currentChunk, newTime);
				}
			}
		});

		keyboardCommand('ctrl+]', 'Move current chunk forward  second', () => {
			const currentChunk = getCurrentChunk();
			const nextChunk = Chunks.getChunkAfter(currentChunk.time);
			const newTime = currentChunk.time + 0.5;
			if (!nextChunk || nextChunk.time > newTime){
				setCurrentTime(newTime);
				Chunks.move(currentChunk, newTime);
			}
		});

		keyboardCommand('ctrl+d', 'Delete current chunk', () => {
			const currentChunk = getCurrentChunk();
			if (currentChunk.time > 0){
				Chunks.delete(currentChunk.time);
			}
		});

		keyboardCommand(`ctrl+;`, 'Rewind 1 second', () => {
			setCurrentTime(getCurrentTime() - 1);
		});

		keyboardCommand(`ctrl+'`, 'Fast-forward 1 second', () => {
			setCurrentTime(getCurrentTime() + 1);
		});

		keyboardCommand('ctrl+n', 'Set chunk start time to current time', () => {
			const chunk = getCurrentChunk();
			Chunks.move(chunk, getCurrentTime());
		});

		keyboardCommand('ctrl+/', 'Split chunk at cursor and current time', () => {
			const currentInput = document.activeElement;
			if (currentInput && currentInput.tagName === 'TEXTAREA' && currentInput.selectionStart === currentInput.selectionEnd){
				const text = currentInput.value;
				const pos = currentInput.selectionStart;
				const textBeforeCursor = text.substring(0, pos - 1).replace(/\s+$/, '');
				const textAfterCursor = text.substring(pos).replace(/^\s+/, '');

				const currentChunk = getCurrentChunk();
				Chunks.save(currentChunk.set('text', textBeforeCursor));

				const newChunk = Chunks.create({ time: getCurrentTime(), text: textAfterCursor, speaker: currentChunk.speaker });
				Chunks.save(newChunk);
			}
		});

		keyboardCommand('ctrl+z', 'Undo the last change you made anywhere in the program (covers more cases than regular undo)', globalUndo);

		keyboardCommand('ctrl+r', 'Rename speaker', () => {
			const speakers = DataCore.get('/speakers');
			const chunk = getCurrentChunk();
			const currentName = speakers.get(chunk.speaker);

			const wasPlaying = isPlaying();
			pause();

			const newName = window.prompt('Enter the name of the current speaker:', currentName || '');

			if (wasPlaying){
				play();
			}

			if (newName !== null){
				DataCore.set('/speakers',
					newName
					? speakers.set(chunk.speaker, newName)
					: speakers.delete(chunk.speaker)
				);
			}
		});

		// So people can see what keys are being pressed (good for demos)
		this.run('highlight active keyboard commands',
			commandWasRun
			.groupBy(keys => keys)
			.map(observable => observable.debounce(1000))
			.mergeAll()
			.do(keys => this.setState({ activeCommands: this.state.activeCommands.remove(keys) }))
		);
	},

	render(){
		if (this.state.visible){
			return <table className='shortcuts'>
				<tbody>
					{
						this.commands
						.entrySeq()
						.map(([keys, desc]) =>
							<tr key={keys} className={`shortcut ${this.state.activeCommands.has(keys) ? 'active' : 'inactive'}`}>
								<td className='shortcut-key'>{keys.replace(/^ctrl\+/, 'control ')}</td>
								<td>&nbsp;&nbsp;&nbsp;{desc}</td>
							</tr>
						)
					}
				</tbody>
				<tbody>
					<tr className='shortcut'>
						<td className='shortcut-key'>control 1-9</td>
						<td>&nbsp;&nbsp;&nbsp;Set speaker for current chunk</td>
					</tr>
				</tbody>
			</table>;
		}
		else {
			return <div className='hidden-shortcuts'>Press control k to see the keyboard shortcuts</div>;
		}
	},
});

module.exports = CommandsView;



