const Immutable = require('immutable');
const React = require('react');
const ReactUtils = require('react-utils');
const Rx = require('rx');
const DataCore = require('data-core');

const PlayerView = React.createClass({
	mixins: [ReactUtils.RxMixin, React.addons.LinkedStateMixin],

	getInitialState(){
		return {
			showHelp: false
		};
	},

	componentDidMount(){
		DataCore.set('/player', React.findDOMNode(this.refs.player));

		// Start "auto-pause while typing" functionality
		const inputEvent = Rx.Observable.fromEvent(window, 'input').share();
		const blurEvent = Rx.Observable.create(observer => {
			const handler = e => observer.onNext(e);
			window.addEventListener('blur', handler, true);
			return Rx.Disposable.create(() => window.removeEventListener('blur', handler, true));
		});

		let paused = false;

		this.run('auto pause when typing',
			DataCore.observe('/playing')
			.filter(b => b)
			.do(() => paused = false)
			.take(1)
			// the player is playing, start listening for inputEvent:
			.flatMap(inputEvent)
			.do(() => {
				if (!paused){
					paused = true;
					pause();
					this.autoPausedAt = getCurrentTime() - 1.5; // courtesy rewind a bit
				}
			})
			.debounce(1000)
			.do(() => {
				if (paused){
					paused = false;
					const currentChunk = getCurrentChunk();
					if (this.autoPausedAt && this.autoPausedAt > currentChunk.time){
						setCurrentTime(this.autoPausedAt);
					}
					else {
						setCurrentTime(currentChunk.time);
					}
					play();
				}
			})
			.take(1)
			.takeUntil(
				Rx.Observable.merge(
					blurEvent,
					DataCore.observe('/playing').filter(b => b).skipUntil(inputEvent),
				)
			)
			.repeat()
		);
	},

	render(){
		return <div>
			<div className='player'>
				<audio controls ref='player'/>
				<a href='' className='help-button' onClick={this.toggleHelp}>Help</a>
			</div>
			{
				this.state.showHelp &&
				<div className='help'>
					<div className='important'>
						IMPORTANT: All data is saved in your web browser on your computer. Therefore, if you clear your browser data, you will lose your saved transcriptions.
					</div>
					<ul>
						<li>This program was tested in Chrome on Mac. It may not work well in other browsers. Use Chrome for best results.</li>
						<li>Drag an audio file into the page to get started.</li>
						<li>Everything is saved automatically as you type. You will not lose any information if you close the browser window.</li>
						<li>To pick up where you left off last time, just drag the same file back into the page.</li>
						<li>The idea is to cut the file up into small chunks, typically around 30 seconds each.</li>
						<li>The start of each chunk is synchronized with the audio, so as you click/tab between chunks, the audio is automatically positioned to the start of the chunk.</li>
						<li>The audio will pause when you start typing. When you stop typing, the audio will continue (with a small overlap so you can keep your bearings).</li>
						<li>You do not have to transcribe the entire file from start to finish. Feel free to fast-forward to any part of the audio, create a new chunk there, and start transcribing.</li>
						<li>To get your transcription out of this program, click the "Copy" button. Then, just paste into whatever other writing program youre using.</li>
						<li>In order to maximize productivity, everything is done with keyboard shortcuts. Once you load an audio file, press control k to see the shortcuts.</li>
						<li>Enjoy!</li>
					</ul>
					<div className='copyright'>Copyright  2015 Dan Farino. All Rights Reserved.</div>
				</div>
			}
		</div>;
	},

	toggleHelp(e){
		this.setState({ showHelp: !this.state.showHelp });
		e.preventDefault();
	}
});

module.exports = PlayerView;



