require('main-style');
require('mousetrap');

const Immutable = require('immutable');
const Rx = require('rx');
const React = require('react');
const ReactUtils = require('react-utils');
const DataCore = window.DataCore = require('data-core');
// DataCore.logWrites = true;
DataCore.set('/__webpack_hash__', __webpack_hash__);
const Chunks = require('chunks');


window.formatTime = function formatTime(time){
	const withoutFrac = Math.trunc(time);
	const sec = `0${withoutFrac % 60}`.slice(-2);
	const min = `0${Math.trunc((withoutFrac / 60) % 60)}`.slice(-2);
	const hours = `0${Math.trunc(withoutFrac / 3600)}`.slice(-2);
	const frac = `0${Math.trunc(time * 10)}`.slice(-1);

	return `${hours}:${min}:${sec}.${frac}`;
};

window.getPlayer = function getPlayer(){
	return DataCore.get('/player');
};

window.getCurrentTime = function getCurrentTime(){
	return DataCore.get('/time');
};

window.setCurrentTime = function setCurrentTime(time){
	const newTime = +Math.max(time, 0).toFixed(2);
	getPlayer().currentTime = newTime; // assertion: this is the ONLY place that sets the current player's .currentTime property!
	currentTimeHint.onNext(newTime);
};

window.getCurrentChunk = function getCurrentChunk(){
	return Chunks.get(getCurrentTime());
};

window.play = function play(){
	getPlayer().play();
};

window.pause = function pause(){
	getPlayer().pause();
};

window.isPlaying = function isPlaying(){
	return !getPlayer().paused;
};

window.tryGetFromLocalStorage = function tryGetFromLocalStorage(key, defaultValue){
	if (!localStorage.hasOwnProperty(key)){
		return defaultValue;
	}

	try{
		return JSON.parse(localStorage[key]);
	}
	catch (e){
		return defaultValue;
	}
};


const Sha1 = require('js-sha1');
const CurrentFileInfo = require('current-file-info');

const DataCoreView = require('data-core-view');
const ChunksView = require('chunks-view');
const CurrentFileInfoView = require('current-file-info-view');
const PlayerView = require('player-view');
const UndoView = require('undo-view');
const CommandsView = require('commands-view');
const ClipboardButton = require('clipboard-button');

require('load-and-save');
require('settings');

DataCore.set('/speakers', Immutable.Map());

const currentTimeHint = new Rx.Subject; // since the timeupdate event seems to be async with respect to setting .currentTime

function readPlayerTime(player){
	return +player.currentTime.toFixed(2); // assertion: this is the ONLY place that reads the current player's .currentTime property!
}

DataCore.provide('/time',
	DataCore.observe('/player')
	.flatMapLatest(player =>
		player
		? Rx.Observable.fromEvent(player, 'timeupdate')
			.map(() => readPlayerTime(player))
			.merge(currentTimeHint)
			.startWith(readPlayerTime(player))
		: Rx.Observable.just(null)
	)
);

DataCore.provide('/playing',
	DataCore.observe('/player')
	.flatMapLatest(player =>
		player
		? Rx.Observable.merge(
				Rx.Observable.fromEvent(player, 'playing'),
				Rx.Observable.fromEvent(player, 'pause')
			)
			.map(() => !player.paused)
		: Rx.Observable.just(false)
	)
	.startWith(false)
);

const MainUI = React.createClass({
	mixins: [ReactUtils.RxMixin, ReactUtils.DataCoreMixin],

	getInitialState(){
		return {
			loading: null
		};
	},

	componentWillMount(){
		this.bindToDataCore({
			file: '/file'
		});

		window.addEventListener('dragover', e => e.preventDefault());

		window.addEventListener('drop', e => {
			e.stopPropagation();
			e.preventDefault();

			if (e.dataTransfer.files.length){
				const file = e.dataTransfer.files[0];
				this.setState({ loading: file.name });

				const reader = new FileReader;
				reader.onload = () => {
					const sha1 = Sha1(reader.result);
					const objectUrl = URL.createObjectURL(file);
			    getPlayer().src = objectUrl;
			    DataCore.set('/file', new CurrentFileInfo({ name: file.name, sha1: sha1 }));
					this.setState({ loading: null });
				};
				reader.readAsArrayBuffer(file);
		  }
		});
	},

	render(){
		return <div>
			{
				this.state.loading &&
				<div className='loading'>Loading {this.state.loading}...</div>
			}
			{
				localStorage.show_core &&
				<DataCoreView/>
			}
			<PlayerView/>
			{
				this.state.file &&
				<ClipboardButton/>
			}
			<CurrentFileInfoView file={this.state.file}/>
			{
				this.state.file &&
				<div>
					<UndoView/>
					<CommandsView/>
					<ChunksView/>
				</div>
			}
		</div>;
	}
});

React.render(<MainUI/>, document.getElementById('react-mount'));



