const React = require('react');
const ReactUtils = require('react-utils');
const Rx = require('rx');
const DataCore = require('data-core');
const Chunks = require('chunks');

const ClipboardButton = React.createClass({
	mixins: [ReactUtils.RxMixin, ReactUtils.DataCoreMixin],

	componentWillMount(){
		this.bindToDataCore({
			speakers: '/settings/copy-speakers',
			timestamps: '/settings/copy-timestamps'
		});

		this.flashMessage = new Rx.Subject;

		this.run('handle flash message',
			this.flashMessage
			.flatMapLatest(msg => {
				this.setState({ msg });
				return Rx.Observable.timer(2000)
				.do(() => this.setState({ msg: null }));
			})
		);
	},

	componentDidMount(){
		const button = React.findDOMNode(this.refs.copy);
		const client = new ZeroClipboard(button);
		client.on('copy', e => {
			try{
				const clipboard = e.clipboardData;
				const speakers = DataCore.get('/speakers');
				var lastSpeaker = null;
				const allText =
					Chunks.getAll()
					.map(chunk => {
						var text = '';
						if (this.state.speakers && chunk.speaker != lastSpeaker){
							text = speakers.get(chunk.speaker) + '\n';
							lastSpeaker = chunk.speaker;
						}
						if (this.state.timestamps){
							text += `[${formatTime(chunk.time)}] `;
						}
						return text + chunk.text;
					})
					.join('\n\n');
				clipboard.setData('text/plain', allText);
				this.flashMessage.onNext({ text: 'Copied successfully!' });
			}
			catch (e){
				console.error(e);
				this.flashMessage.onNext({ error: String(e) });
			}
		});
	},

	render(){
		const msg = this.state && this.state.msg;
		return <div className='copy-button'>
			{
				msg &&
				msg.error &&
				<span className='error'>{msg.error}</span>
			}
			{
				msg &&
				msg.text &&
				<span className='text'>{msg.text}</span>
			}
			<button onClick={e => e.preventDefault()} ref='copy'>Copy</button>

			<div className='options'>
				<div>
					<label>
						copy with speakers
						<input type='checkbox'
						       checked={this.state.speakers}
						       onChange={e => this.setSetting('speakers', e.target.checked)}/>
					</label>
				</div>
				<div>
					<label>
						copy with timestamps
						<input type='checkbox'
						       checked={this.state.timestamps}
						       onChange={e => this.setSetting('timestamps', e.target.checked)}/>
					</label>
				</div>
			</div>
		</div>;
	},

	setSetting(name, value){
		DataCore.set(`/settings/copy-${name}`, value);
	}
});

module.exports = ClipboardButton;



