const Immutable = require('immutable');
const React = require('react');
const ReactUtils = require('react-utils');
const DataCore = require('data-core');
const Chunks = require('chunks');

window.globalUndo = function(){};

const UndoView = React.createClass({
	mixins: [ReactUtils.RxMixin, ReactUtils.DataCoreMixin, ReactUtils.ImmutableRenderMixin],

	componentWillMount(){
		this.observeIntoState({
			frames:
				DataCore.observe('/current/file')
				.flatMapLatest(
					DataCore.observeMany({
						chunks: '/chunks/all',
						speakers: '/speakers',
						settings: '/settings'
					})
					.eventLoopDebounce()
					.map(o => Immutable.Map(o))
					.map(frame => {
						const prev = (this.state && this.state.frames) || Immutable.List();
						if (Immutable.is(this.undoingTo, frame)){
							delete this.undoingTo;
							return prev.pop();
						}
						else {
							return prev.push(frame);
						}
					})
					.startWith(Immutable.List())
				)
		});

		window.globalUndo = this.undo;
	},

	render(){
		return null;
	},

	undo(){
		const frame = this.state.frames.pop().last();
		if (frame){
			this.undoingTo = frame;

			Chunks.set(frame.get('chunks'));
			DataCore.set('/speakers', frame.get('speakers'));
			DataCore.set('/settings', frame.get('settings'));
		}
	}
});

module.exports = UndoView;



