const Immutable = require('immutable');
const React = require('react');
const ReactUtils = require('react-utils');
const Rx = require('rx');
const Chunks = require('chunks');

const ChunkView = React.createClass({
	mixins: [ReactUtils.RxMixin, ReactUtils.ImmutableRenderMixin, React.addons.LinkedStateMixin],

	componentWillMount(newProps){
		this.run('handle being current',
			this.propObserver('current')
			.filter(b => b)
			.flatMap(this.observeRenderCompleted())
			.do(() => React.findDOMNode(this.refs.textarea).focus())
		);

		this.run('auto size textarea',
			Rx.Observable.merge(
				this.propObserver('chunk')
				.flatMap(
					this.observeRenderCompleted()
					.flatMap( // <-- this flatMap mess here "fixes" a bug that causes incorrect textarea sizing when a file first loads
						Rx.Observable.just(true)
						.observeOn(Rx.Scheduler.timeout)
						.startWith(true)
					)
				),
				Rx.Observable.fromEvent(window, 'resize')
			)
			.do(() => {
				const textarea = React.findDOMNode(this.refs.textarea);
				if (textarea){
					const oldScrollY = window.scrollY;
					textarea.style.height = '2em';
					textarea.style.height = `${textarea.scrollHeight}px`;
					window.scrollTo(window.scrollX, oldScrollY);
				}
			})
		);
	},

	render(){
		const chunk = this.props.chunk;
		const currentClass = this.props.current ? 'current' : '';
		const doneClass = chunk.done ? 'done' : '';

		let speakers = this.props.speakers;
		if (!speakers.has(chunk.speaker)){
			speakers = speakers.set(chunk.speaker, '');
		}

		return <tr className={`chunk ${currentClass} ${doneClass}`}>
			<td className='checkCol'>
				<input className='mousetrap' tabIndex='-1' type='checkbox' checked={chunk.done} onChange={e => Chunks.save(chunk.set('done', e.target.checked))}/>
			</td>
			<td className='speakerCol'>
				<select onChange={e => Chunks.save(chunk.set('speaker', e.target.value))}
				        value={chunk.speaker}
				        tabIndex='-1'
				        title='This indicates the current speaker. See the keyboard shortcuts for more info.'>
					{
						speakers
						.entrySeq()
						.map(([n, speaker]) => <option key={n} value={n}>{`${n}. ${speaker}`}</option>)
					}
				</select>
			</td>
			<td className='editorCol'>
				<textarea className='editor mousetrap'
				          ref='textarea'
				          onFocus={this.onFocus}
				          value={chunk.text}
				          onChange={e => Chunks.save(chunk.set('text', e.target.value))}
				/>
			</td>
			<td className='timeCol'>{formatTime(chunk.time)}</td>
		</tr>;
	},

	onFocus(){
		if (!this.props.current){
			setCurrentTime(this.props.chunk.time);
		}
	}
});

module.exports = ChunkView;



