const Chunks = require('chunks');
const Immutable = require('immutable');
const DataCore = require('data-core');

function localStorageKeyForFile(file){
	return `transcription_${file.sha1}`;
}

DataCore.observe('/file')
.filter(o => o)
.map(o => o.toJS())
.flatMapLatest(file => {
	// We have a new file. Load data.
	const saved = localStorage[localStorageKeyForFile(file)];
	if (saved){
		try{
			const parsed = JSON.parse(saved);
			Chunks.set(
				Immutable.Seq(parsed.chunks)
				.map(c => Chunks.create(c))
				.toList()
			);

			DataCore.set('/speakers', Immutable.Map(parsed.speakers));
		}
		catch (e){
			console.error(`error loading saved chunks for ${file.sha1}`, e);
			Chunks.reset();
			DataCore.set('/speakers', Immutable.Map());
		}
	}
	else {
		Chunks.reset();
	}

	// Now, start auto-save running
	return DataCore.observeMany({
		chunks: '/chunks/all',
		speakers: '/speakers'
	})
	.debounce(1000)
	.do(inputs => localStorage[localStorageKeyForFile(file)] = JSON.stringify(inputs))
})
.run('load and save');



